// import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const contactApi = createApi({
  reducerPath: 'contactApi',
  baseQuery: fetchBaseQuery({
    baseUrl: 'https://64aadf5c0c6d844abedeef07.mockapi.io',
  }),
  tagTypes: ['contacts'], //   i i, -i(1.) ii 
  endpoints: builder => ({
    getAllContacts: builder.query({
      query: () => '/contacts',
      providesTags: ['contacts'], //   i i,      i   ii ,     -     
      // providesTags      ,      Redux state(     )
    }),
    createContact: builder.mutation({
      query: data => ({
        url: '/contacts',
        method: 'POST',
        body: data,
      }),
      invalidatesTags: ['contacts'], //    'contacts'        - 
    }), // for POST requests
    deleteContact: builder.mutation({
      query: contactId => ({
        url: `contacts/${contactId}`,
        method: 'DELETE',
      }),
      invalidatesTags: ['contacts'],
    }), // for DELETE requests
  }),
});

// Export hooks for usage in functional components, which are
// auto-generated based on the defined endpoints
export const {
  useGetAllContactsQuery,
  useCreateContactMutation,
  useDeleteContactMutation,
} = contactApi; // use + "custom builder Name" + Query -   ,  GET-.  POST-: use + "custom builder Name" + Mutation
